<template>
  <div id="translateform" class="row">
    <div class="col-md-6 col-md-offset-3" id="box">
      <form v-on:submit="translate" class="well form-inline">
          <input type="text" v-model="translateTxt" class="form-control">
          <select v-model="language" class="form-control">
              <option value="en">English</option>
              <option value="kn">Kannada</option>
              <option value="zh">Chinese</option>
              <option value="ko">Korean</option>
              <option value="de">German</option>
              <option value="fr">French</option>
              <option value="ja">Japanese</option>
          </select>
          <input type="submit" value="翻译" class="btn btn-success">
      </form>
    </div>
  </div>
</template>
<script>
  export default{
    name:"translateform",
    data(){
      return {
          translateTxt:"",
          language:""
      }
    },
    methods:{
      translate:function(e){
          if(this.translateTxt==""){
            alert("请输入翻译文本");
          }else{
            this.$emit('translateTxt',this.translateTxt,this.language);
          }
          e.preventDefault();
      }
    },
    created(){
      this.language='en';
    }
  }
</script>
<style>
  .btn{
    margin: 10px;
  }
</style>


