<template>
    <div id="app">
        <h1>在线翻译</h1>
        <h5 class="text-muted">简单 / 便捷 / 高效</h5>
        <translateform v-on:translateTxt="translateTxt"></translateform>
        <translateout v-bind:translated="translated"></translateout>
    </div>
</template>

<script>
    import TranslateForm from './components/TranslateForm'
    import TranslateOut from './components/TranslateOut'

    export default{
        name:"vue",
        data(){
            return {
                translated:""
            }
        },
        methods:{
            translateTxt:function(txt,lang){
                this.$http.get("https://translate.yandex.net/api/v1.5/tr.json/translate?key=trnsl.1.1.20180324T082528Z.6d8d4f5020e4cb4f.90039c13fb5a96e5dce9755235743d0de2834113&lang="+lang+"&text="+txt)
                .then((response)=>{
                    this.translated=response.body.text[0];
                })
            }
        },
        components:{
            'translateform':TranslateForm,
            'translateout':TranslateOut
        }
    }
</script>

<style>
    #app{
        font-family: 'Avenir' , Helvetica , Arial , sans-serif;
        -webkit-font-smoothing:antialiased;
        -moz-osx-font-smoothing:grayscale;
        text-align: center;
        color: #2c3e50;
        margin-top: 60px;
    }
</style>


